.product {
	padding: 20px;
}

.results img {
	margin-right: 4px;
}

.imageDetails .infoPanel a {
	text-decoration: none;
	color: rgb(121, 30, 30);
}

.imageDetails .controls span {
	z-index: 2000;
	font-size: 18pt;
	font-weight: bold;
	top: 100px;
	cursor: pointer;
	background-color: rgba(100, 100, 100, 0.5);
	color: #eee;
	width: 100px;
	padding: 5px;
	text-decoration: none;
	display: inline-block;
	text-align: center;
}

.imageDetails .controls .prev {
	border-radius: 50%;
	background-color: #acacac;
	color: black;
}
.imageDetails .controls .prev:hover {
	background-color: #ddd;
	color: black;
}
.imageDetails .controls .next {
	border-radius: 50%;
	background-color: rgb(68, 30, 112);
	color: white;
}
.imageDetails .controls .next:hover {
	background-color: rgb(121, 37, 133);
	color: white;
}

.resultsList {
	padding: 0;
	margin: 0;
}

.imageDetails img {
	position: right;
	cursor: pointer;
	max-width: 30vw;
}

.imageBox {
	width: 60vw;
	margin: 0 auto;
	display: flex;
}

.left-half {
	display: inline-block;
	width: 48%;
}

.right-half {
	display: inline-block;
	width: 40%;
	height: 100%;
	text-align: left;
	/* text-decoration: cyan; */
	/* box-shadow: 1px 1px 16px rgba(0, 0, 0, 0.3); */
}

.reviewBox {
	display: inline-block;
	width: 60%;
	height: 100%;
	text-align: left;
	color: cornsilk;
	background-color: rgb(39, 34, 51);
}

.container {
	max-width: 750px;
	margin-top: 10px;
}
